import React,{useState,createContext} from 'react';
export const CountryContext = createContext();

export const CountryProvider = props =>{
    const query = new URLSearchParams(window.location.search);
    console.log(query.toString());
    const [page, setPage] = useState(1);
    const [selected, setSelected] = useState([]);
    let country = null;
    if (window.location.pathname.slice(1,10)==='retailers') {
      country = window.location.pathname.slice(11);
    }else if (window.location.pathname === '/search'||window.location.pathname === '/admin/products') {
      country = query.get('country');
    }

    const countryInRetailer = (window.location.pathname).slice()
    const [countryInfo, setCountryInfo] = useState({
      // name:'United Kingdom',
      name:(country === null)?'United Kingdom':country,
      //Chaged
    })
    return(
      <CountryContext.Provider value = {[countryInfo, setCountryInfo ,selected, setSelected,page,setPage]}>
        {props.children}
      </CountryContext.Provider>
    );
  }