import React from 'react';

export const DeleteOneFeed = (id,active) => {
    (active==="active"||active === "pending")?active = "false":active = "true";
    const newurl = `https://modacompris.com/api/feeds/${id}/${active}`;
    fetch(newurl, {
      method: 'Delete',
      headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
      },
    }).then(res => res.json()).then((response) => {
        if(response.results === 'ok'){
            // alert("This Feed was deleted successfully.");
        } else if(response.results === 'Error'){
            // alert("Failure deleting. Please try again.");
        }
    }).catch(error => {
         alert("Failure deleting. Please try again.");
    });
    
  }
  