import React,{useState,useEffect,useContext} from 'react';
import ProductArea from '../../../Components/ProductArea/AdminProductArea'
import MenuBar from '../../../Components/MenuBar/MenuBar'
import Pagination from '@material-ui/lab/Pagination';
import { Grid} from '@material-ui/core';
import Container from '@material-ui/core/Container'
import Footer from '../../../Components/Footer/Footer'
import Loading from '../../../Components/Loading/Loading'
import NullPage from '../../../Components/NullPage/NullPage'
import {AdminBackendQuery} from '../../../Pages/Search/AdminBackendQuery'
import Header from './AdminProductHeader';
import {CountryContext} from '../../../Components/Header/CountryContext'

const useFetchProducts = (search,searchTerm,country)=>{
  const [products, setProducts] = useState(null);
  const [loading, setLoading] = useState(true);
  const [returnVal, setReturnVal] = useState(null);
  const [totalCnt, setTotalCnt] = useState(0);
  const [menuData, setMenuData] = useState(null);
  
  useEffect(() => {
    async function fetchData (){
      const url = `https://modacompris.com/api/products?${search}`
      const response = await fetch(url);
      const data = await response.json();
      const products = data.products;
      const [result] = data.results;
      const [totalCnt] = data.total;
      
          setProducts(products);
          setLoading(false);
          setReturnVal(result);
          setTotalCnt(totalCnt);
    
          if (result ==='ok' || result === 'no') {
            setMenuData({category:data.category[0],size:data.size[0],brand:data.brand[0]});
          }
    }
    fetchData();
  }, [search,searchTerm,country]);

  return { products ,setProducts,menuData,setMenuData, loading, returnVal,totalCnt,};
}

export default function Products({location}) {
  const [countryInfo, setCountryInfo,,,page,setPage] = useContext(CountryContext);

  let searchQuery = new URLSearchParams(location.search);
  let country = searchQuery.get('country');
  const searchTerm = searchQuery.get('q');
  if (country===null) {
    searchQuery.set('country',countryInfo.name);
  }
  const {products,setProducts,menuData,setMenuData, loading, returnVal, totalCnt} = useFetchProducts(AdminBackendQuery(searchQuery, page),searchTerm,country);
  const handleChange = (event, value) => {
    setPage(value);
  };

  return (
    <div>
      <React.Fragment>
          <Header/>
          {loading?<Loading/>:
            <Container maxWidth = 'lg'>
              {(returnVal === 'no results' || returnVal === 'no') ? 
                <NullPage/>:
                menuData &&
                <MenuBar 
                menu = {[menuData,setMenuData]} 
                />
                }
              <div></div>
              { products && <ProductArea products = { [products,setProducts]}/>}
                <Grid container spacing={3} direction = "row" justify = "flex-end">
                    <Grid item>
                      { products && <Pagination count={ Math.ceil(totalCnt/12) } shape="rounded" page = { page } onChange = { handleChange } />}
                    </Grid>
                </Grid>
              <Footer/>
            </Container>
          }
        </React.Fragment>     
    </div>
    
  );
}