function isTextValid(str) {
    if(str == null) return false;
    const value = str.trim();
    let flag = true;
    if (value) {
      flag = true;
    } else {
      flag = false;
    }
    return flag;
}

function isEmailValid(str){
  return new RegExp(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,15}/g).test(str);
}

const TEST_SITE_KEY = "6Lfp5-kUAAAAAL6zgVweflCawd3mXS59PkA8_oLB";

export default function Contact_us() {

    const classes = useStyles();

    const [businessName, setBusinessName] = useState(null);
    const [errorBusinessName, setErrorBusinessName] = useState('#FFFFFF');
    
    const [websiteUrl, setWebsiteUrl] = useState(null);
    const [errorWebsiteUrl, setErrorWebsiteUrl] = useState('#FFFFFF');

    const [msg, setMsg] = useState(null);
    const [errorMsg, setErrorMsg] = useState('#FFFFFF');

    const [subject, setSubject] = useState(null);
    const [errorSubject, setErrorSubject] = useState('#FFFFFF');

    const [emailAddress, setEmailAddress] = useState(null);
    const [errorEmail, setErrorEmail] = useState('#FFFFFF');

    const [checkSubmit, setSubmit] = useState(false);
    const [errorCaptcha, setErrorCaptcha] = useState('#FFFFFF');



    const onChange = (e)=>{
        if( e == null ){
            setSubmit(false);
            setErrorCaptcha('#FF1744');
        } else {
            setSubmit(true);
            setErrorCaptcha('#FFFFFF');
        } 
    }

    const handleChange = (e) => {
        switch(e.target.name){
            case 'businessname':
                setBusinessName(e.target.value);
                if(isTextValid(e.target.value)){
                    setErrorBusinessName('#FFFFFF');
                } else {
                    setErrorBusinessName('#FF1744');
                }
                return;
            case 'websiteurl':
                setWebsiteUrl(e.target.value);
                if(e.target.value != null && isUrlValid(e.target.value)){
                    setErrorWebsiteUrl('#FFFFFF');
                } else {
                    setErrorWebsiteUrl('#FF1744');
                }
                return;
            case 'subject':
                setSubject(e.target.value);
                if(isTextValid(e.target.value)){
                    setErrorSubject('#FFFFFF');
                } else {
                    setErrorSubject('#FF1744');
                }
                return;
            case 'message':
                setMsg(e.target.value);
                if(isTextValid(e.target.value)){
                    setErrorMsg('#FFFFFF');
                } else {
                    setErrorMsg('#FF1744');
                }
                return;
            case 'email':
                setEmailAddress(e.target.value);
                if(isEmailValid(e.target.value)){
                    setErrorEmail('#FFFFFF');
                } else {
                    setErrorEmail('#FF1744');
                }
                return;
        }
    }

    const handleEnterDown = (e) => {
        if(e.key === 'Enter'){
            e.preventDefault();
            switch(e.target.name){
                case 'businessname':
                    setBusinessName(e.target.value);
                    if(isTextValid(e.target.value)){
                        setErrorBusinessName('#FFFFFF');
                    } else {
                        setErrorBusinessName('#FF1744');
                    }
                    return;
                case 'websiteurl':
                    setWebsiteUrl(e.target.value);
                    if(e.target.value != null && isUrlValid(e.target.value)){
                        setErrorWebsiteUrl('#FFFFFF');
                    } else {
                        setErrorWebsiteUrl('#FF1744');
                    }
                    return;
                case 'subject':
                    setSubject(e.target.value);
                    if(isTextValid(e.target.value)){
                        setErrorSubject('#FFFFFF');
                    } else {
                        setErrorSubject('#FF1744');
                    }
                    return;
                case 'message':
                    setMsg(e.target.value);
                    if(isTextValid(e.target.value)){
                        setErrorMsg('#FFFFFF');
                    } else {
                        setErrorMsg('#FF1744');
                    }
                    return;
                case 'email':
                    setEmailAddress(e.target.value);
                    if(isEmailValid(e.target.value)){
                        setErrorEmail('#FFFFFF');
                    } else {
                        setErrorEmail('#FF1744');
                    }
                    return;
            }
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        
        let isAllReady = true;

        // check businessname

        if(!isTextValid(businessName)){
            isAllReady = false;
            setErrorBusinessName('#FF1744');
        }

        // check the website url

        if(websiteUrl == null || !isUrlValid(websiteUrl)){
            isAllReady = false;
            setErrorWebsiteUrl('#FF1744');
        }

        // check the email address
        if(!isEmailValid(emailAddress)){
            isAllReady = false;
            setErrorEmail('#FF1744');
        }

        // check the subject

        if(!isTextValid(subject)){
            isAllReady = false;
            setErrorSubject('#FF1744');
        }

        // check the message

        if(!isTextValid(msg)){
            isAllReady = false;
            setErrorMsg('#FF1744');
        }

        // check the reCAPTCHA

        if(!checkSubmit){
            isAllReady = false;
            setErrorCaptcha('#FF1744');
        }

        if(!isAllReady){
            return;
        } else {
            
            const templateParams = {
                "reply_to": emailAddress,
                "from_name": businessName,
                "to_name": "ModaComprise",
                "message_html": msg,
                "subject": subject,
                "businessName":businessName,
                "websiteURL":websiteUrl,
            };
        
            emailjs.send('microlion116', 'template_8WECszqD', templateParams, 'user_4EfCkzHj97NafO2AIpMfZ')
              .then((result) => {
                  if (result.text==='OK') {
                      alert("Your message was successfully sent.");
                  }else{
                      alert("Please fill correct information.");
                  }
              }, (error) => {
              });
        }
    }

    return (
        <React.Fragment>
            <MetaTags>
                <title>Contact  ModaCompris Comparison Shopping Site CSS</title>
                <meta name="description" content="Contact ModaCompris comparison shopping site (CSS) to find out more about how you can get more traffic to your e-commerce website." />
                <meta property="og:title" content="Contact  ModaCompris Comparison Shopping Site CSS" />
            </MetaTags>
                <CssBaseline />
            <Header/>
            <Container maxWidth = "md">
                <Grid xs = {12} container justify = 'center' alignItems = 'center' >
                    <Grid xs = {10} item >
                        <Box  mt = {8} mb = {4}>
                            <Typography variant="h5" color="textPrimary" align="center">
                                To add your feed to this site please use the form <Link href = "/add_feed" underline = "always" >here.</Link><br/>

                                To remove your feed from this site please send an email to shopping@wearepolymer.com<br/>
                                For other support please use the following contact details.<br/>
                                Email: shopping@wearepolymer.com<nbsp/> Telephone: +44 20 3920 7456
                            </Typography>
                        </Box>
                    </Grid>
                </Grid> 

                <Box> 
                    <form onSubmit={(e)=>handleSubmit(e)} noValidate>
                    {/* <Typography >If you have business or other questions, please fill out the following form to contact us. Thank you.</Typography> */}
                        <Grid container spacing={4} alignItems = "center" justify = "flex-start">
                            <Grid item xs={12} md = {6} >
                                <TextField
                                    id = "outlined-bare"
                                    fullWidth
                                    required
                                    label = "Business Name"
                                    name = "businessname"
                                    value = { businessName }
                                    autoComplete = "off"
                                    type = "name"
                                    variant = "outlined"
                                    onChange = { handleChange } 
                                    onKeyDown = { (e) => handleEnterDown(e) }
                                />
                                <FormHelperText>
                                    <Typography variant = "h5" style = {{color: errorBusinessName}} >
                                        Please input your business name.
                                    </Typography>
                                </FormHelperText>
                            </Grid>

                            <Grid item xs={12} sm = {6} >
                                <TextField
                                    id = "outlined-bare"
                                    fullWidth
                                    required
                                    label = "Website URL"
                                    name = "websiteurl"
                                    value = { websiteUrl }
                                    autoComplete = "off"
                                    onChange = { handleChange }
                                    onKeyDown = { (e) => handleEnterDown(e) }
                                    variant = "outlined"
                                    inputProps = {{ 'aria-label': 'bare' }}
                                />
                                <FormHelperText>
                                    <Typography variant = "h5" style = {{color: errorWebsiteUrl}} >
                                        Please input your website url correctly. ex : https://my.example.com
                                    </Typography>
                                </FormHelperText>
                            </Grid>
                            
                            <Grid item xs={12} sm = {12}>
                                <TextField
                                    required
                                    fullWidth
                                    label = "Email Address"
                                    name = 'email'
                                    value = { emailAddress }
                                    autoComplete = "off"
                                    onChange={ handleChange }
                                    onKeyDown={ (e) => handleEnterDown(e) }
                                    type="email"
                                    variant="outlined"
                                />
                                <FormHelperText>
                                    <Typography variant = "h5" style = {{color: errorEmail}} >
                                        Please input your email address correctly.
                                    </Typography>
                                </FormHelperText>
                            </Grid>

                            <Grid item xs={12} sm = {12}>
                                <TextField
                                    id = "outlined-bare"
                                    fullWidth
                                    required
                                    error = {errorSubject === '#FFFFFF'?false:true}
                                    label = 'Subject'
                                    name = 'subject'
                                    value = { subject }
                                    autoComplete = "off"
                                    onChange={ handleChange }
                                    onKeyDown={ (e) => handleEnterDown(e) }
                                    variant = "outlined"
                                    inputProps = {{ 'aria-label': 'bare' }}
                                />
                                <FormHelperText>
                                    <Typography variant = "h5" style = {{color: errorSubject}} >
                                        Please input your subject of message.
                                    </Typography>
                                </FormHelperText>
                            </Grid>

                            <Grid item xs = {12}>
                                {/* <Captcha/> */}
                                <ReCAPTCHA
                                    sitekey = {TEST_SITE_KEY}
                                    onChange = { onChange }
                                />
                           
                                    <Typography variant = "h5" style = {{color: errorCaptcha}} >
                                        Invalid captcha. Please try again.
                                    </Typography>
                            </Grid>
                        </Grid>
                        <Box display="flex" flexDirection="column" mt = {5}>
                            <Box mb={1} my = {5}>
                                <TextField
                                    variant="outlined"
                                    multiline
                                    placeholder="Get in touch with us"
                                    inputProps={{ "aria-label": "Get in Touch" }}
                                    InputProps={{
                                      className: classes.whiteBg
                                    }}
                                    rows={4}
                                    fullWidth
                                    required
                                    label = 'Message'
                                    name = 'message'
                                    value = { msg }
                                    autoComplete = "off"
                                    onChange={ handleChange }
                                    onKeyDown={ (e) => handleEnterDown(e) }
                                />
                                <FormHelperText>
                                    <Typography variant = "h5" style = {{color: errorMsg}} >
                                        Please input your message.
                                    </Typography>
                                </FormHelperText>
                            </Box>
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                onSubmit = { handleSubmit }
                                color="primary"
                                >
                                  Send Message
                            </Button>
                        </Box>
                    </form>
                </Box>

                <Footer/>            
            </Container>
        </React.Fragment>
    );
}