import  React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { makeStyles} from '@material-ui/core/styles';
import Home from './Pages/Home';
import Product from './Pages/Search/Product';
import About from './Pages/about';
import FAQ from './Pages/Faq';
import AddFeed from './Pages/AddFeed/AddFeed';
import Contact from './Pages/Contactus';
import Privacy from './Pages/Privacy/Privacy';
import Main from './Pages/Admin/Dashboard';
import Retailer_country from './Pages/Retailers';
import Retailer from './Pages/Retailers';
import decode from 'jwt-decode';
// import { Router, Link } from "@reach/router"

import AdminProduct from './Pages/Admin/Products/AdminProduct';
import Admin from './Pages/Admin/Admin';
import AdminRetailer from './Pages/Admin/Retailers/AdminRetailer';
import Feed from '././Pages/Admin/Feeds/Feeds';
import FeedProducts from './Pages/Admin/Feeds/FeedProducts';
import Settings from './Pages/Admin/Settings/Settings';
import ReactGA from 'react-ga';
import {CountryProvider} from './Components/Header/CountryContext'

const useStyles = makeStyles(theme => ({
  pageContainer:{
    msOverflowX:'hidden',
    marginRight: 'calc(-1 * (100vw - 100%))',
  },
}));

const trackingId = "UA-155997554-2"; // Replace with your Google Analytics tracking ID
ReactGA.initialize(trackingId);
ReactGA.pageview(window.location.pathname + window.location.search);

function initializeReactGA() {
  ReactGA.initialize('UA-155997554-2');
  ReactGA.pageview('/search');
}

const checkAuth = () => {
  const token = localStorage.getItem('token');
  const refreshToken = localStorage.getItem('refreshToken');

  if(!token || !refreshToken){
    return false;
  }

  try{
    const { exp } = decode(token);
    if(exp < new Date().getTime() / 1000) {
      const newurl = `https://modacompris.com/api/login/token`;
      fetch(newurl, {
          method: 'POST',
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
          },
          body: JSON.stringify({
              token: refreshToken
          })
      }).then(res => res.json()).then((response) => {
        if(response.result == 'ok'){
          localStorage.setItem("token", response.token);
          return true;
        } else {
          return false;
        }
      })
      .catch(err => {
        return false;
      });      
    }
  } catch(err) {
    return false;
  }

  return true;
}

const AuthRoute = ({component: Component, ...rest}) => (
  <Route {...rest} render = {props => (
    checkAuth() ? (
      <Component {...props} />
    ) : (
      <Redirect to={{ pathname: '/admin' }} />
    )
  )} />
)

export default function ProminentAppBar() {
  const classes = useStyles();

  return (
     <div className = {classes.pageContainer}>
       <CountryProvider>

        <Router>
            <Switch>
                  <Route exact path="/" component = {Home}/>
                  <Route exact path = "/faq" component = {FAQ}/>
                  <Route exact path="/about" component = {About}/>
                  <Route exact path = '/add_feed' component = {AddFeed}/>
                  <Route exact path="/search" component = {Product}/>
                  <Route exact path="/privacy" component = {Privacy}/>
                  <Route exact path="/contact" component = {Contact}/>
                  <Route exact path="/main" component = {Main}/>
                  <Route exact path = "/retailers" component = {Retailer}/>
                  <Route  path = "/retailers/:country" component = {Retailer_country} />

                  <Route exact path='/admin' component={Admin} />
                  <AuthRoute exact path='/admin/retailers' component = { AdminRetailer } />
                  <AuthRoute exact path='/admin/products' component = { AdminProduct } />
                  <AuthRoute exact path = '/admin/feeds' component = {Feed}/>
                  <AuthRoute exact path = "/admin/feeds/:id" component = {FeedProducts}/>
                  <AuthRoute exact path = "/admin/settings" component = {Settings}/>
                  
            </Switch> 
        </Router>
       </CountryProvider>

     </div>
  );
}
