import React,{useState,useEffect,useContext} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { lighten, makeStyles } from '@material-ui/core/styles';
import {useTheme} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterListIcon from '@material-ui/icons/FilterList';
import Header from '../../../Components/Header/AdminHeader'
import { Container, Button } from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useHistory } from "react-router-dom";
import IconButton from '@material-ui/core/IconButton'
import Active from '@material-ui/icons/Visibility'
import Inactive from '@material-ui/icons/VisibilityOff'
import {DeleteOneFeed} from './DeleteOneFeed'
import MobileFeed from './FeedMobile'
import withWidth from '@material-ui/core/withWidth'
import {green} from '@material-ui/core/colors'
import Pending from '@material-ui/icons/HourglassEmpty'
import {CountryContext} from '../../../Components/Header/CountryContext'

const useFetch = ()=>{
  const [feedData, setFeedData] = useState(null);
  
  useEffect(() => {
    async function fetchData (){
      const url = `https://modacompris.com/api/feeds/0`
      const response = await fetch(url);
      const data = await response.json();
      setFeedData(data);
    }
    fetchData();  
  }, []);
  return { feedData,setFeedData};
}

function useWidth() {
    const theme = useTheme();
    const keys = [...theme.breakpoints.keys].reverse();
    return (
      keys.reduce((output, key) => {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const matches = useMediaQuery(theme.breakpoints.up(key));
        return !output && matches ? key : output;
      }, null) || 'xs'
    );
  }
  
function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

const headCells = [
  { id: 'feed_url', numeric: false, disablePadding: true, label: 'Feed URL' },
  { id: 'vendor_businessname', numeric: false, disablePadding: false, label: 'Business Name' },
  { id: 'vendor_email', numeric: false, disablePadding: false, label: 'Email Adress' },
  { id: 'feed_new', numeric: false, disablePadding: false, label: 'Feed Status' },
  { id: 'vendor_country', numeric: false, disablePadding: false, label: 'Country' },
];

function EnhancedTableHead(props) {
  const { classes, onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        <TableCell padding="checkbox">
          {/* <Checkbox
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{ 'aria-label': 'select all desserts' }}
          /> */}
        </TableCell>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'left'}
            padding={headCell.disablePadding ? 'none' : 'default'}
            sortDirection={orderBy === headCell.id ? order : false}
            style = {{fontWeight:700}}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
              {orderBy === headCell.id ? (
                <span className={classes.visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </span>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
        <TableCell padding = "checkbox"/>
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  classes: PropTypes.object.isRequired,
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  title: {
    flex: '1 1 100%',
  },
}));

const EnhancedTableToolbar = (props) => {
  const classes = useToolbarStyles();
  const { numSelected } = props;

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      {numSelected > 0 ? (
        <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
          {numSelected} selected
        </Typography>
      ) : (
        <Typography className={classes.title} variant="h6" id="tableTitle" component="div">
          Nutrition
        </Typography>
      )}

      {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton aria-label="delete">
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      ) : (
        <Tooltip title="Filter list">
          <IconButton aria-label="filter list">
            <FilterListIcon />
          </IconButton>
        </Tooltip>
      )}
    </Toolbar>
  );
};

EnhancedTableToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired,
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: 750,
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  tableContainer:{
    marginTop:"50px"
  }
}));

export default function EnhancedTable() {
  const classes = useStyles();
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('calories');
  const [selected, setSelected] = useState([]);
  const [page, setPage] = useState(0);
  const [dense, setDense] = useState(false);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [countryInfo,setCountryInfo] = useContext(CountryContext)

  const {feedData,setFeedData} = useFetch();
  const history = useHistory();
  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = feedData.map((feed) => feed.feed_url);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangeDense = (event) => {
    setDense(event.target.checked);
  };

  const isSelected = (name) => selected.indexOf(name) !== -1;
  const handleClickRow = (event,id) =>{
    
    history.push(`/admin/feeds/${id}`);
  }

  const emptyRows = feedData&&(rowsPerPage - Math.min(rowsPerPage, feedData.length - page * rowsPerPage));

  const handleDeleteOneFeed = (id,active)=>{
    let newFeedData = [...feedData];
    (active==="active"||active==="pending")? newFeedData.find(feed=>feed.id==id).feed_new = "cancelled":newFeedData.find(feed=>feed.id==id).feed_new = "active";
    setFeedData(newFeedData);
    DeleteOneFeed(id,active);

  }
  return (
    <div className={classes.root}>
     <Header/>
        {
          feedData&&
          <Container className = {classes.tableContainer}>
            <Paper className={classes.paper}>
            {/* <EnhancedTableToolbar numSelected={selected.length} /> */}
            <TableContainer>
            <Table
                className={classes.table}
                aria-labelledby="tableTitle"
                size={dense ? 'small' : 'medium'}
                aria-label="enhanced table"

            >
                <EnhancedTableHead
                classes={classes}
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={handleSelectAllClick}
                onRequestSort={handleRequestSort}
                rowCount={feedData.length}
                />
                <TableBody>
                {stableSort(feedData, getComparator(order, orderBy))
                    .filter(item=>{
                      if (countryInfo.name==='All countries') {
                        return item;
                      }else{
                       return item.vendor_country===countryInfo.name
                      }
                    })
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((row, index)=> {
                    const isItemSelected = isSelected(row.feed_url);
                    const labelId = `feed-${index}`;
                    return (
                      <TableRow
                        hover
                        role="checkbox"
                        aria-checked={isItemSelected}
                        tabIndex={-1}
                        key={row.feed_url}
                        selected={isItemSelected}
                        >
                        {/* <TableCell padding="checkbox">
                            <Checkbox
                            checked={isItemSelected}
                            inputProps={{ 'aria-labelledby': labelId }}
                            />
                        </TableCell> */}
                        <TableCell padding = "checkbox"/>
                        {/* <TableCell align="left" onClick = {(event)=>handleClickRow(event,row.id)}>{row.id}</TableCell> */}

                        <TableCell component="th" id={labelId} scope="row" padding="none"  onClick = {(event)=>handleClickRow(event,row.id)}>
                            {row.feed_url}
                        </TableCell>
                        <TableCell align="left" onClick = {(event)=>handleClickRow(event,row.id)}>{row.vendor_businessname}</TableCell>
                        <TableCell align="left" onClick = {(event)=>handleClickRow(event,row.id)}>{row.vendor_email}</TableCell>
                        <TableCell align="left" onClick = {(event)=>handleClickRow(event,row.id)}>{(row.feed_new==="cancelled"?"Inactive":row.feed_new)}</TableCell>
                        <TableCell align="left" onClick = {(event)=>handleClickRow(event,row.id)}>{row.vendor_country}</TableCell>
                        <TableCell align = "left">

                          {(row.feed_new=="active")&&
                            <IconButton onClick = {()=>handleDeleteOneFeed(row.id,row.feed_new)} style = {{padding:0}}>
                              <Active style = {{padding:0,color:green[500]}}/>
                            </IconButton>
                            }
                            {(row.feed_new=="cancelled")&&
                              <IconButton onClick = {()=>handleDeleteOneFeed(row.id,row.feed_new)} style = {{padding:0}}>
                                <Inactive style = {{padding:0}}/>
                              </IconButton>
                            }
                            {(row.feed_new=="pending")&&
                              <IconButton onClick = {()=>handleDeleteOneFeed(row.id,row.feed_new)} style = {{padding:0}}>
                                <Pending style = {{padding:0}}/>
                              </IconButton>
                            }
                            
                          
                        </TableCell>
                        </TableRow>
                    );
                    })}
                {emptyRows > 0 && (
                    <TableRow style={{ height: (dense ? 33 : 53) * emptyRows }}>
                    <TableCell colSpan={6} />
                    </TableRow>
                )}
                </TableBody>
            </Table>
            </TableContainer>
            <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={feedData.filter(item=>{
              if (countryInfo.name==='All countries') {
                return item;
              }else{
               return item.vendor_country===countryInfo.name
              }
            }).length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
            />
        </Paper>
        <FormControlLabel
            control={<Switch checked={dense} onChange={handleChangeDense} />}
            label="Dense padding"
        />
        <Button> In Active No Product</Button>
        
        </Container>
        }
     
    </div>
  );
}
