import React,{useState,useEffect} from 'react';
import ProductArea from '../../../Components/ProductArea/AdminProductArea'
import Container from '@material-ui/core/Container'
import Header from '../../../Components/Header/AdminHeader'


const useFetch = (id)=>{
    const [products, setProducts] = useState(null);
    
    useEffect(() => {
      async function fetchData (){
        const url = `https://modacompris.com/api/feeds/${id}`
        const response = await fetch(url);
        const data = await response.json();
        setProducts(data.products);
      }
  
      fetchData();
    }, [id]);
  
    return { products,setProducts};
  }

function FeedProducts({match}) {

    const{products,setProducts} = useFetch(match.params.id);
    const show = products&&products.map((item)=>{return item.product_show})
    return (
        <div>
            <Header/>
            <Container>
                {products&&<ProductArea feed_id = {match.params.id} products = {[products,setProducts]} product_show = {show}/>}
            </Container>
        </div>

    )
}

export default FeedProducts;
