function isUserNameValid(userName){
    let formIsValid = true;

    //Name
    if(userName == null){
      formIsValid = false;
    }

    if(typeof userName !== "undefined"){
      if(userName != null && !userName.match(/^[a-zA-Z]+$/)){
          formIsValid = false;
      }        
    }

    return formIsValid;
}

function isBusinessNameValid(str) {
    if(str == null) return false;
    const value = str.trim();
    let flag = true;

    if (value) {
      flag = true;
    } else {
      flag = false;
    }

    return flag;
}

function isEmailValid(str){
    return new RegExp(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,15}/g).test(str);
}

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}


export default function EnhancedTable() {
    const classes = useStyles();
    const [order, setOrder] = React.useState('asc');
    const [orderBy, setOrderBy] = React.useState('name');
    const [selected, setSelected] = React.useState([]);
    const [page, setPage] = React.useState(0);
    const [dense, setDense] = React.useState(false);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    const [rows, setRows] = useState([]);
    const [fetchState, setFetchState] = useState(true);
    const [drawerState,setDrawerState] = useState(false);
    const history = useHistory();
    // const [indexCountry,setIndexCountry] = useContext(CountryContext)
    const [countryInfo,setCountryInfo] = useContext(CountryContext)


    const [userBusinessName, setUserBusinessName] = useState(null);
    const [errorUserBusinessName, setErrorUserBusinessName] = useState('#FFFFFF');
    const [userWebsiteUrl, setUserWebsiteUrl] = useState(null);
    const [errorUserWebsiteUrl, setErrorUserWebsiteUrl] = useState('#FFFFFF');
    const [userFirstName, setUserFirstName] = useState(null);
    const [errorUserFirstName, setErrorUserFirstName] = useState('#FFFFFF');
    const [userLastName, setUserLastName] = useState(null);
    const [errorUserLastName, setErrorUserLastName] = useState('#FFFFFF');
    const [userEmail, setUserEmail] = useState(null);
    const [errorUserEmail, setErrorUserEmail] = useState('#FFFFFF');
    const [userCountry, setUserCountry] = useState(null);
    const [errorUserCountry, setErrorUserCountry] = useState('#FFFFFF');
    const [userPhoneNumber, setUserPhoneNumber] = useState('');
    const [errorUserPhoneNumber, setErrorUserPhoneNumber] = useState('#FFFFFF');
    const [checkSubmit, setSubmit] = useState(false);
    const [open, setOpen] = useState(false);
    const [openText, setOpenText] = useState("");
    const [openType, setOpenType] = useState("error");

    const [modalStyle] = React.useState(getModalStyle);
    const [openModal, setOpenModal] = React.useState(false);

    // All retailer data fetching at start ...
    const [rowProperty, setRowProperty] = useState(null);
    
    if(fetchState) {
        const newurl = `https://modacompris.com/api/vendors/0`;
        fetch(newurl, { method: "Get" }).then(res => res.json()).then((response) => {
            const myRetailers = response.results;
            let rows_temp = [];
            for(let i = 0; i < myRetailers.length; i++){
                rows_temp.push(
                    createData(
                        myRetailers[i].vendor_id, 
                        myRetailers[i].vendor_businessname, 
                        myRetailers[i].vendor_websiteurl, 
                        myRetailers[i].vendor_firstname + " " + myRetailers[i].vendor_lastname, 
                        myRetailers[i].vendor_email,
                        myRetailers[i].vendor_country
                    )
                );
            }
            setRows(rows_temp);
            setFetchState(false);
        }).catch(error => {
            // alert(error.message);
        });
    }

    const handleRequestSort = (event, property) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
          const newSelecteds = rows.map((n) => n.name);
          setSelected(newSelecteds);
          return;
        }
        setSelected([]);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);
        let newSelected = [];

        if (selectedIndex === -1) {
          newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
          newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
          newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
          newSelected = newSelected.concat(
            selected.slice(0, selectedIndex),
            selected.slice(selectedIndex + 1),
          );
        }

        setSelected(newSelected);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handleChangeDense = (event) => {
        setDense(event.target.checked);
    };

    // individual retailer showing.
    const handleClickRow = (id) => {
        setDrawerState(true);
        const newurl = `https://modacompris.com/api/vendors/${id}`;
        fetch(newurl, { method: "Get" }).then(res => res.json()).then((response) => {
            setRowProperty(response.results);
            setUserBusinessName(response.results.businessname[0]);
            setUserWebsiteUrl(response.results.websiteurl[0]);
            setUserFirstName(response.results.firstname[0]);
            setUserLastName(response.results.lastname[0]);
            setUserCountry(response.results.country[0]);
            setUserEmail(response.results.email[0]);
            setUserPhoneNumber(response.results.phonenumber[0]);
        }).catch(error => {
            // alert(error.message);
        });
    }

    // retailer updating
    const handleUpdateRetailer = (id) => {
        let isAllReady = true;

        // check businessname

        if(userBusinessName == null || userBusinessName == "" ||
            ( userBusinessName && isBusinessNameValid(userBusinessName))){
            if(userBusinessName == null){
                setUserBusinessName("");
            }
            setErrorUserBusinessName('#FFFFFF');
        } else  {
            isAllReady = false;
            setErrorUserBusinessName('#FF1744');
        }

        // check the username

        if(userFirstName == null || userFirstName == '' ||
            (userFirstName && isUserNameValid(userFirstName))){
            if(userFirstName == null) setUserFirstName("");
            setErrorUserFirstName('#FFFFFF');
        } else {
            isAllReady = false;
            setErrorUserFirstName('#FF1744');
        }

        if(userLastName == null || userLastName =='' || 
            (userLastName && isUserNameValid(userLastName))){
            if(userLastName == null) setUserLastName("");
            setErrorUserLastName('#FFFFFF');
        } else {
            isAllReady = false;
            setErrorUserLastName('#FF1744');
        }

        // check the website url

        if(userWebsiteUrl == null || (userWebsiteUrl && !isUrlValid(userWebsiteUrl))){
            isAllReady = false;
            setErrorUserWebsiteUrl('#FF1744');
        }

        // check the email address
        if(userEmail == null || userEmail == '' || 
            (userEmail && isEmailValid(userEmail))){
            if(userEmail == null) setUserEmail('');
            setErrorUserEmail('#FFFFFF');
        } else {
            isAllReady = false;
            setErrorUserEmail('#FF1744');
        }

        // check the phone number
        if(userPhoneNumber == null || userPhoneNumber == ""){
            if(userPhoneNumber == null) setUserPhoneNumber('');
            setErrorUserPhoneNumber('#FFFFFF');
        } else {
            const pn = parsePhoneNumberFromString(userPhoneNumber);
            if(pn != null){
                if(!pn.isPossible() || !pn.isValid()){
                    isAllReady = false;
                    setErrorUserPhoneNumber('#FF1744');
                } 
            }  else {
                isAllReady = false;
                setErrorUserPhoneNumber('#FF1744');
            }
        }
      
      
        // check the country name
        if(typeof userCountry === 'string' && userCountry.length > 3){
            setErrorUserCountry('#FFFFFF');
        } else {
            isAllReady = false;
            setErrorUserCountry('#FF1744');
        }
        if(isAllReady){
            setSubmit(false);
            const newurl = `https://modacompris.com/api/vendors/${id}`;
            fetch(newurl, {
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    businessname: userBusinessName,
                    websiteurl: userWebsiteUrl,
                    firstname: userFirstName,
                    lastname: userLastName,
                    email: userEmail,
                    phonenumber: userPhoneNumber,
                    country_name: userCountry,
                })
            }).then(res => res.json()).then((response) => {
                if(response.results == 'Error'){
                    setOpenType("error");
                    setOpenText("The attempt was failed. Please try again.");
                } else if(response.results == 'ok'){
                    setDrawerState(false);
                    setFetchState(true);
                    setOpenType("success");
                    setOpenText("This retailer was updated successfully.");
                }
                setOpen(true);
            }).catch(error => {
                setOpen(true);
                setOpenType("error");
                setOpenText("The attempt was failed. Please try again.");
            }); 
        }
    }

   

    const handleChange = (event) => {
        switch(event.target.name){
            case 'businessname':
                setUserBusinessName(event.target.value);
                if(event.target.value == "" || event.target.value == null || isBusinessNameValid(event.target.value)){
                    setErrorUserBusinessName('#FFFFFF');
                } else {
                    setErrorUserBusinessName('#FF1744');
                }
                return;
            case 'websiteurl':
                setUserWebsiteUrl(event.target.value);
                if(event.target.value != null && isUrlValid(event.target.value)){
                    setErrorUserWebsiteUrl('#FFFFFF');
                } else {
                    setErrorUserWebsiteUrl('#FF1744');
                }
                return;
            case 'firstname':
                setUserFirstName(event.target.value);
                if(event.target.value == "" || event.target.value == null || isUserNameValid(event.target.value)){
                    setErrorUserFirstName('#FFFFFF');
                } else {
                    setErrorUserFirstName('#FF1744');
                }
                return;
            case 'lastname':
                setUserLastName(event.target.value);
                if(event.target.value == "" || event.target.value == null || isUserNameValid(event.target.value)){
                    setErrorUserLastName('#FFFFFF');
                } else {
                    setErrorUserLastName('#FF1744');
                }
                return;
            case 'email':
                setUserEmail(event.target.value);
                if(event.target.value == "" || event.target.value == null || isEmailValid(event.target.value)){
                    setErrorUserEmail('#FFFFFF');
                } else {
                    setErrorUserEmail('#FF1744');
                }
                return;
            case 'country_name':
                setUserCountry(event.target.value);
                return;
            case 'phonenumber':
                if(event.target.value == "" || event.target.value == null){
                    setErrorUserPhoneNumber('#FFFFFF');
                } else {
                    let strNum = new AsYouType().input(event.target.value);
                    setUserPhoneNumber(strNum);
                    const pn = parsePhoneNumberFromString(strNum);
                    if(pn != null){
                        if(!pn.isPossible() || !pn.isValid()){
                            setErrorUserPhoneNumber('#FF1744');
                        } else {
                            setErrorUserPhoneNumber('#FFFFFF');
                        }
                    }  else {
                        setErrorUserPhoneNumber('#FF1744');
                    }
                }
                return;
        }
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const handleEnterDown = (event) => {     
        if (event.key === "Enter") {
            event.preventDefault();
            switch(event.target.name){
                case 'businessname':
                    setUserBusinessName(event.target.value);
                    if(event.target.value == "" || event.target.value == null || isBusinessNameValid(event.target.value)){
                        setErrorUserBusinessName('#FFFFFF');
                    } else {
                        setErrorUserBusinessName('#FF1744');
                    }
                    return;
                case 'websiteurl':
                    setUserWebsiteUrl(event.target.value);
                    if(event.target.value != null && isUrlValid(event.target.value)){
                        setErrorUserWebsiteUrl('#FFFFFF');
                    } else {
                        setErrorUserWebsiteUrl('#FF1744');
                    }
                    return;
                case 'firstname':
                    setUserFirstName(event.target.value);
                    if(event.target.value == "" || event.target.value == null || isUserNameValid(event.target.value)){
                        setErrorUserFirstName('#FFFFFF');
                    } else {
                        setErrorUserFirstName('#FF1744');
                    }
                    return;
                case 'lastname':
                    setUserLastName(event.target.value);
                    if(event.target.value == "" || event.target.value == null || isUserNameValid(event.target.value)){
                        setErrorUserLastName('#FFFFFF');
                    } else {
                        setErrorUserLastName('#FF1744');
                    }
                    return;
                case 'email':
                    setUserEmail(event.target.value);
                    if(event.target.value == "" || event.target.value == null || isEmailValid(event.target.value)){
                        setErrorUserEmail('#FFFFFF');
                    } else {
                        setErrorUserEmail('#FF1744');
                    }
                    return;
                case 'country_name':
                    setUserCountry(event.target.value);
                    return;
                case 'phonenumber':
                    if(event.target.value == "" || event.target.value == null){
                        setErrorUserPhoneNumber('#FFFFFF');
                    } else {
                        let strNum = new AsYouType().input(event.target.value);
                        setUserPhoneNumber(strNum);
                        const pn = parsePhoneNumberFromString(strNum);
                        if(pn != null){
                            if(!pn.isPossible() || !pn.isValid()){
                                setErrorUserPhoneNumber('#FF1744');
                            } else {
                                setErrorUserPhoneNumber('#FFFFFF');
                            }
                        }  else {
                            setErrorUserPhoneNumber('#FF1744');
                        }
                    }
                    return;
            }
        }
    }

    // retailer deleting
    const handleDeleteRetailer = (id) => {
        const newurl = `https://modacompris.com/api/vendors/${id}`;
        fetch(newurl, {
          method: 'Delete',
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
          },
        }).then(res => res.json()).then((response) => {
            if(response.results == 'ok'){
                let newVal = [];
                for(let i = 0; i < rows.length; i++){
                  if(rows[i].id != id){
                    newVal.push(rows[i]);
                  }
                }
                setRows(newVal);
                setOpen(true);
                setOpenText("This retailer was deleted successfully.");
                setOpenType("success");
            } else if(response.results == 'Error'){
                setOpen(true);
                setOpenText("Failure deleting. Please try again.");
                setOpenType("error");
            }
        }).catch(error => {
            setOpen(true);
            setOpenText("Failure deleting. Please try again.");
            setOpenType("error");
        });
        setDrawerState(false);
        setOpenModal(false);
        setUserCountry(null);
    }

    const toggleDrawer = (open) => event => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
      setDrawerState(open);
      if(open === false){
          setUserCountry(null);
      }
    };

    const isSelected = (name) => selected.indexOf(name) !== -1;

    const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);
    const gotoFeeds = (id)=>{
      history.push(`/admin/feeds/${id}`);
    }

    const handleOpenModal = () => {
        setOpenModal(true);
    };
  
    const handleCloseModal = () => {
      setOpenModal(false);
    };

    const bodyModal = (
        <div style={ modalStyle } className={ classes.paperModal }>
            <h2 id="simple-modal-title">Delete This Retailer?</h2>
            <p id="simple-modal-description">
                You'll permanantly lose this retailer's
                <br/>
                - personal information
                <br/>
                - feed URLs
                <br/> 
                - products information
            </p>
            <Grid container justify = 'center'>
                <Grid item xs = {1}></Grid>
                <Grid item xs = {6} justify = 'center'>
                    <Button
                        onClick = { handleCloseModal }
                        variant="contained"
                        name = "no"
                        className={classes.buttonU}
                      >
                        Cancel
                    </Button>
                </Grid>
                <Grid item xs = {5} justify = 'center'>
                    <Button
                        onClick = { () => handleDeleteRetailer(rowProperty.id)}
                        variant="contained"
                        name = "yes"
                        className={classes.buttonD}
                      >
                        Delete
                    </Button>
                </Grid>
                
            </Grid>
        </div>
    );
    
    return (
        <>
            <Header/>
            <div className={classes.snackroot}>
                <Snackbar open={open} autoHideDuration={1500} onClose={handleClose}>
                    {
                        <Alert onClose={handleClose} severity={openType}>
                            {openText}
                        </Alert>
                    }
                </Snackbar>
            </div>
            <Modal
                open={ openModal }
                // onClose={ handleCloseModal }
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
              >
                { bodyModal }
            </Modal>

            <div style = {{width: '100%'}}>
                <div className={classes.root}>

                <Container maxWidth="lg">
                        <Paper className={classes.paper}>
                            {/* <EnhancedTableToolbar numSelected={selected.length} /> */}
                            <TableContainer>
                                <Table
                                  className={classes.table}
                                  aria-labelledby="tableTitle"
                                  size={dense ? 'small' : 'medium'}
                                  aria-label="enhanced table"
                                  >
                                    <EnhancedTableHead
                                      classes={classes}
                                      numSelected={selected.length}
                                      order={order}
                                      orderBy={orderBy}
                                      onSelectAllClick={handleSelectAllClick}
                                      onRequestSort={handleRequestSort}
                                      rowCount={rows.length}
                                    />
                                    <TableBody>
                                        {stableSort(rows, getComparator(order, orderBy))
                                          .filter(item=>{
                                            if (countryInfo.name==='All countries') {
                                                return item;
                                            }else{
                                                return item.country===countryInfo.name;
                                            }
                                          })
                                          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                          .map((row, index) => {
                                              const isItemSelected = isSelected(row.name);
                                              const labelId = `enhanced-table-checkbox-${index}`;

                                              return (
                                                  <TableRow
                                                    hover
                                                    onClick={() => handleClickRow(row.id)}
                                                    role="checkbox"
                                                    aria-checked={isItemSelected}
                                                    tabIndex={-1}
                                                    key={row.name}
                                                    selected={isItemSelected}
                                                    >
                                                      {/* <TableCell padding="checkbox">
                                                          <Checkbox
                                                            checked={isItemSelected}
                                                            inputProps={{ 'aria-labelledby': labelId }}
                                                          />
                                                      </TableCell> */}
                                                      <TableCell component="th" id={labelId} scope="row" align="left" style = {{marginLeft : 40}}>
                                                        {row.businessname}
                                                      </TableCell>
                                                      <TableCell style = {{marginLeft:40}} align="left">{row.websiteurl}</TableCell>
                                                      <TableCell style = {{marginLeft:40}} align="left">{row.retailername}</TableCell>
                                                      <TableCell style = {{marginLeft:40}} align="left">{row.email}</TableCell>
                                                      <TableCell style = {{marginLeft:40}} align="left">{row.country}</TableCell>
                                                  </TableRow>
                                              );
                                          })
                                        }
                                        {emptyRows > 0 && (
                                            <TableRow style={{ height: (dense ? 33 : 53) * emptyRows }}>
                                                <TableCell colSpan={6} />
                                            </TableRow>
                                        )}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            <TablePagination
                              rowsPerPageOptions={[10, 15, 20]}
                              component="div"
                              count={rows.filter(item=> item.country===countryInfo.name).length}
                              rowsPerPage={rowsPerPage}
                              page={page}
                              onChangePage={handleChangePage}
                              onChangeRowsPerPage={handleChangeRowsPerPage}
                            />
                        </Paper>

                        
                        { rowProperty && (rowProperty !== 'Error') &&
                            < Drawer anchor = {"right"} open = {drawerState} className = {classes.drawer} onClose={toggleDrawer(false)} transitionDuration = {700}>
                                <IconButton onClick = {toggleDrawer(false)} style = {{backgroundColor:'rgb(0,0,0,0.3)',padding:0,margin:10, width:30, height:30,}}>
                                    <CloseIcon style = {{color:'#FFF'}}/>
                                </IconButton>
                            <Paper className = {classes.drawer}>
                                <Grid container spacing={3} alignItems = "center" justify = "flex-start">
                                    {/* <Typography style = {{marginLeft: 30, marginTop:16, marginBottom:12,}} variant = 'h4'>
                                          Business Name :
                                    </Typography> */}
                                    {/* <Paper component="form" className={classes.textpaper}> */}
                                        {/* <Typography style = {{marginLeft: 10, marginTop:8, marginBottom:8,}} variant = 'h5'>
                                            {rowProperty.businessname}
                                        </Typography> */}
                                        <Grid item xs={11} style = {{margin: 'auto',}}>
                                          <TextField
                                              id="outlined-bare"
                                              fullWidth
                                              label = "Business Name"
                                              name = "businessname"
                                              value = { userBusinessName }
                                              autoComplete = "off"
                                              type = "name"
                                              variant = "outlined"
                                              onChange = { handleChange } 
                                              onKeyDown = { (e) => handleEnterDown(e) }
                                          />
                                          <FormHelperText>
                                              <Typography variant = "h5" style = {{color: errorUserBusinessName}} >
                                                  Please input your business name.
                                              </Typography>
                                          </FormHelperText>
                                        </Grid>
                                   
                                    <Grid item xs={11} style = {{margin: 'auto',}} >
                                        <TextField
                                            id="outlined-bare"
                                            fullWidth
                                            required
                                            label = "Website URL"
                                            name = "websiteurl"
                                            value = { userWebsiteUrl }
                                            autoComplete = "off"
                                            onChange = { handleChange }
                                            onKeyDown = { (e) => handleEnterDown(e) }
                                            variant = "outlined"
                                            inputProps = {{ 'aria-label': 'bare' }}
                                        />
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{color: errorUserWebsiteUrl}} >
                                                Please input your website url correctly. <br/> Ex : https://my.example.com
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>

                                    <Grid item xs={11} style = {{margin: 'auto',}}>
                                        <TextField
                                            id="outlined-bare"
                                            fullWidth
                                            label= 'First Name'
                                            name = 'firstname'
                                            value = { userFirstName }
                                            autoComplete = "off"
                                            onChange={handleChange}
                                            onKeyDown={ (e) => handleEnterDown(e) }
                                            variant="outlined"
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{ color: errorUserFirstName }} >
                                                Please input your first name.
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>
                                        
                                    <Grid item xs={11} style = {{margin: 'auto',}}>
                                        <TextField
                                            id="outlined-bare"
                                            fullWidth
                                            label = "Last Name"
                                            name = 'lastname'
                                            value = { userLastName }
                                            autoComplete = "off"
                                            onChange = { handleChange }
                                            onKeyDown = { (e) => handleEnterDown(e) }
                                            variant="outlined"
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{ color: errorUserLastName }} >
                                                Please input your last name.
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>

                                    <Grid item xs={11} style = {{margin:'auto',}}>
                                        <TextField
                                            variant="outlined"
                                            fullWidth
                                            label = "Email Address"
                                            name = 'email'
                                            value = { userEmail }
                                            autoComplete = "off"
                                            onChange={ handleChange }
                                            onKeyDown={ (e) => handleEnterDown(e) }
                                            type="email"
                                        />
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{color: errorUserEmail}} >
                                                Please input your email correctly.
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>

                                    <Grid item xs={11} style={{margin:'auto',}}>
                                        <TextField
                                            id="outlined-bare"
                                            fullWidth
                                            label = 'Phone Number'
                                            name = 'phonenumber'
                                            value = { userPhoneNumber }
                                            autoComplete = "off"
                                            variant="outlined"
                                            onChange={handleChange}
                                            onKeyDown={ (e) => handleEnterDown(e) }
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{color: errorUserPhoneNumber}} >
                                                Please input your phonenumber correctly.<br/> Ex : +12345678900
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>
                                    <Grid item xs={11} style={{margin:'auto',}}>
                                        {
                                            userCountry && 
                                            <Autocomplete
                                                id = "combo-box-country"
                                                options = { countries }
                                                defaultValue={{name:userCountry.toString()}}
                                                onChange={(event, newValue) => {
                                                    if (newValue) {
                                                      setUserCountry(newValue.name);
                                                      setErrorUserCountry('#FFFFFF');
                                                      return;
                                                    } else {
                                                      setUserCountry('  ');
                                                      setErrorUserCountry('#FF1744');
                                                      return;
                                                    }
                                                }}
                                                
                                                getOptionLabel={(option) => {
                                                    // e.g value selected with enter, right from the input
                                                    if (typeof option === 'string') {
                                                    return option;
                                                    }
                                                    if (option.inputValue) {
                                                    return option.inputValue;
                                                    }
                                                    return option.name;
                                                }}
                                                
                                                fullWidth
                                                renderOption={(option) => option.name}
                                                renderInput={ 
                                                    (params) => <TextField 
                                                                    {...params} 
                                                                    required
                                                                    label="Country" 
                                                                    name = 'country_name'
                                                                    value = { userCountry }
                                                                    autoComplete = "off"
                                                                    onKeyDown={ (e) => handleEnterDown(e) }
                                                                    variant="outlined" 
                                                                />
                                                }
                                            />
                                        }
                                        
                                        <FormHelperText>
                                            <Typography variant = "h5" style = {{color: errorUserCountry}} >
                                                Please select a country.
                                            </Typography>
                                        </FormHelperText>
                                    </Grid>

                                    <Typography style = {{marginLeft: 30, marginTop:16, marginBottom:12,}} variant = 'h4'>
                                          Feed Url :
                                    </Typography>
                                    <Paper component="form" className={classes.textpaper}>
                                      <Grid container xs = {12}>
                                        {
                                            rowProperty.feeds && rowProperty.feeds.length > 0 && rowProperty.feeds[0].map( myfeed =>(
                                              <Grid item xs = {12}>
                                                  <Button 
                                                  style = {{ textTransform : 'none',}}
                                                  onClick = {()=>gotoFeeds(myfeed.id)}
                                                  >
                                                    <Typography style = {{marginTop:8, marginBottom:8,}} variant = 'h5'>
                                                        { myfeed.feed_url }
                                                    </Typography> 
                                                </Button>
                                              </Grid>
                                               
                                            ))
                                        }
                                      </Grid>
                                      
                                        
                                    </Paper>
                                    <>
                                    </>
                                </Grid>
                            </Paper>
                            <Grid container >
                                <Grid item xs = {5}></Grid>
                                <Grid item xs = {3} justify = 'center'>
                                    <Button
                                        onClick = { () => handleUpdateRetailer(rowProperty.id)}
                                        variant="contained"
                                        className={classes.buttonU}
                                      >
                                        Update
                                    </Button>
                                </Grid>
                                <Grid item xs = {3} justify = 'center'>
                                    <Button
                                        // onClick = { () => deleteRetailer(rowProperty.id)}
                                        onClick = {()=> handleOpenModal()}
                                        variant="contained"
                                        className={classes.buttonD}
                                        startIcon={<DeleteIcon />}
                                      >
                                        Delete
                                    </Button>
                                </Grid>
                                <Grid item xs = {1}></Grid>
                            </Grid>
                        </Drawer>
                        }
                    </Container>  
                </div>
            </div>
        </>
    );
}
