import React,{useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography'
import ButtonBase from '@material-ui/core/ButtonBase'
import Header from '../Components/Header/Header'
import Footer from '../Components/Footer/Footer'
import Container from '@material-ui/core/Container' 
import Loding from '../Components/Loading/Loading'  
import Link from '@material-ui/core/Link'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    width:'100%',
    margin:'auto',
  },
  buttonbase:{
    textAlign: 'center',
    width:'100%'
  }
}));

const useFetchRetailers = (country) => {
  const [retailers, setRetailers] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState(null)

  useEffect(() => {
    setLoading(true);
    setError(null);

    country = (country === undefined)? 'United Kingdom':country;
    const apiUrl = `https://modacompris.com/api/vendors/all/${country}`
   
    fetch(apiUrl)
      .then(res => res.json())
      .then(json => {
        setLoading(false)
        if (json.results && json.results !== 'no results') {
          setRetailers(json.results);
        } else {
          setRetailers([]);
        }
      })
      .catch(err => {
        setError(err)
      })
  }, [country])

  return { retailers, loading, error }
}


export default function Retailers({match}){
  console.log(match.params.country)
  const {retailers,loading,error} = useFetchRetailers(match.params.country); 
  
  const classes = useStyles();
  return (
    <div>
      <Header/>
      {
        loading ? <Loding/> :
            <Container maxWidth = 'lg' style = {{marginTop:'10vh'}}>
              <div style = {{minHeight:'35vh'}}>
                <Grid container spacing={3} justify = 'center' >
                  {
                    retailers && 
                    retailers.map((retailer)=>(
                      <Grid item xs={12} sm={6} md = {4} lg = {3} >
                        <ButtonBase className = {classes.buttonbase} href = {retailer.vendor_websiteurl} target="_blank">
                          <Paper component = {Link} className={classes.paper} elevation = {4} style = {{zIndex:-1}}>
                            {!(retailer.vendor_businessname === "null"||retailer.vendor_businessname ==="") ? 
                              <Typography gutterBottom variant="subtitle1" color = 'primary'>
                                {retailer.vendor_businessname}
                              </Typography> :
                              <Typography gutterBottom variant="subtitle1" color = 'primary'>
                                {new URL(retailer.vendor_websiteurl).hostname.split('.')[0]}
                              </Typography>
                            }
                            <Typography variant="body2" gutterBottom>
                              {retailer.vendor_websiteurl.replace(/(.{40})..+/, "$1…")}
                            </Typography>
                          </Paper>
                        </ButtonBase>
                      </Grid>
                    ))
                  }
                </Grid>
              </div>
            <Footer/>
          </Container>
      }        
    </div> 
  );
}
