export const onHandleUpdateProduct = (feed_id,ids,active) => {
    (active==="true")?active = "false":active = "true";
    const newurl = `https://modacompris.com/api/feeds/${active}`;
    return fetch(newurl, {
      method: 'Put',
      headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
      },
      body: JSON.stringify(
            {
                feed_id:feed_id,
                id:ids,
            }
        ),
    }).then(res => res.json()).then((response) => {
        if(response.results == 'ok'){
            return true;
        } else if(response.results == 'Error'){
            return false;

        }
    }).catch(error => {
         alert("Failure deleting. Please try again.");
    });
    
  }
  

