import React,{useState,useContext} from 'react';
import { makeStyles, theme, createStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import { useHistory } from "react-router-dom";
import {CountryContext} from '../Header/CountryContext'

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center',
    },
    input: {
      flex: 1,
      paddingLeft:'10px'
    },
    divider: {
      height: 28,
      margin: 4,
    },
  }),
);

export default function SerchBar({Close}){
  const classes = useStyles();
  const [searchTerm, setSearchTerm] = useState("");
  const [isFocussed, setFocussed] = useState(false);
  const history = useHistory()
  const [countryInfo, setCountryInfo] = useContext(CountryContext);
  const [,,selected, setSelected] = useContext(CountryContext);
  

  const handleEnterDown = event=>{
    if (event.key === "Enter") {
      event.preventDefault();
      
       
      if ((window.location.pathname==="/search")) {
        Close();
      } 
      let newSearchQuery = new URLSearchParams();
        newSearchQuery.set('q',searchTerm);
        newSearchQuery.set('country',countryInfo.name);

        history.push({
          pathname: '/search',
          search:newSearchQuery.toString().toLocaleLowerCase(),
          state: searchTerm
        })

        setCountryInfo(prev=>{
          return{...prev,page:1};
        })
        setSelected([]);
    }
  }
 
  const handleIconButton = ()=>{
    
    if ((window.location.pathname==="/search")) {
      Close();
    } 
    let newSearchQuery = new URLSearchParams();
      newSearchQuery.set('q',searchTerm);
      newSearchQuery.set('country',countryInfo.name)

      history.push({
        pathname: '/search',
        search:newSearchQuery.toString().toLocaleLowerCase(),
        state: searchTerm
      })
      setCountryInfo(prev=>{
        return{...prev,page:1};
      })
      setSelected([]);
  }

  return(
    <Paper component="form" className={classes.root} elevation = {4} >
      <InputBase
        className={classes.input}
        type="search"
        placeholder="What are you looking for?"
        value={searchTerm}
        onClick={() => setFocussed(true)}
        onChange={event => setSearchTerm(event.target.value)}
        onKeyDown={ handleEnterDown }
        autoFocus
        style = {{minHeight:'50px'}}
        fullWidth
      />
      { searchTerm && <IconButton type="button" className={ classes.iconButton } aria-label="search" onClick = { handleIconButton }>
          <SearchIcon />
        </IconButton>
      }
      <Divider className={ classes.divider } orientation="vertical" />
    </Paper>
        
  );

}
