import React,{useState,useContext} from 'react';
import {
  Link
} from "react-router-dom";
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { makeStyles,withStyles } from '@material-ui/core/styles';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Container, Grid, ClickAwayListener, ListItemText } from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';
import ExpandSearch from '../ExpandSearch/AdminExpandSearch';
import Drawer from '../Drawer/AdminDrawer';
import Logo from '../../Components/Logo/AdminLogo'
import IconButton from '@material-ui/core/IconButton';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import SettingsIcon from '@material-ui/icons/Settings';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Grow from '@material-ui/core/Grow';
import Popper from '@material-ui/core/Popper';
import MenuList from '@material-ui/core/MenuList';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import {CountryContext} from './CountryContext';
import Button from '@material-ui/core/Button';


const countries = [
    'United Kingdom',
    'Austria',
    'Belgium',
    'Czech Republic',
    'France',
    'Germany',
    'Greece',
    'Hungary',
    'Ireland',
    'Italy',
    'Netherlands',
    'Poland',
    'Portugal',
    'Romania',
    'Spain',
];


const useStyles = makeStyles(theme => ({
  rightMenu: {
    flexGrow: 1,
    '& > * + *': {
      marginLeft: theme.spacing(1),
    },
    textTransform:'none',
    fontStyle:'normal',
    fontWeight:'normal',
  },
  topBarContainer:{
    msOverflowX:'hidden',
  },
  appbar:{
    boxShadow:'none',
  },
  searchiconbut:{
    '&:hover':{
      background: 'none',
    },
  },
  menuButton:{
    padding:'0px',
  },

  menuIcon:{
    marginLeft:'10px',
  },

  countryButton:{
    textTransform:'none',
    fontStyle:'normal',
    fontWeight:'normal',
   

  },
  logoLargebold:{
  },
  logoSmall:{
    fontSize: '35px',
  },
  searchText:{
    marginRight:10,
  },
  logoLink:{
    color: 'inherit',
    textDecoration: 'inherit',
  },
  
  lg_logoLarge:{
    fontSize: '24px',
  },
  lg_logoLargebold:{
    fontSize: '24px',
  },
  lg_logoSmall:{
    fontSize: '12px',
      
  },
  middle_logoLarge:{
  fontrSize:'35px',
  },
  middle_logoSmall:{
    fontSize:'15px',
  },

  paper: {
    textAlign: 'center',
    // padding:3,
    boxShadow:'none',
    
  },
  topSpace:{
    height:'30px',
  },
  menulink:{
    // color: "#1A1919",
    color: 'inherit',
    textDecoration: 'inherit',
  },

  drawer:{
    width:"30vw",
    minWidth : "350px",
    marginBottom: theme.spacing(2),
    marginTop: theme.spacing(5),
  },
  
}));

export default function ProminentAppBar() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const anchorRef = React.useRef(null);
  const [countryInfo, setCountryInfo,selected, setSelected,page,setPage] = useContext(CountryContext);


  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };
  const handleClickAway = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }
    setOpen(false);
  };
  const handleClose = (index) =>{
    setOpen(false);
    setCountryInfo({name:countries[index]});
    setSelected([]);
    setPage(1);
   
  }
  function handleListKeyDown(event) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    }
  }


  //popover start
  const [openLog, setOpenLog] = React.useState(false);
  const anchorRefLog = React.useRef(null);

  const handleToggleLog = () => {
    setOpenLog((prevOpen) => !prevOpen);
  };

  const handleCloseLog = (event) => {
      if (anchorRefLog.current && anchorRefLog.current.contains(event.target)) {
        return;
      }
      setOpenLog(false);
  };

  function handleListKeyDownLog(event) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpenLog(false);
    }
  }
  // popover end

  return (
      <div style = {{width:'100%'}}>
          <div className = {classes.topBarContainer}>
            <Container maxWidth="lg">
            

              <Hidden smDown>
                  <Grid container direction = "row" justify = "center" alignItems = "center" spacing = {1} style = {{marginTop:"20px"}}> 
                    <Grid item xs={'auto'}>
                      <Typography  align = "left" variant ="h5">
                        Shop in: 
                      </Typography>
                    </Grid>
                    <Grid item xs={2}>
                    <Button
                        ref={anchorRef}
                        aria-controls={open ? 'menu-list-grow' : undefined}
                        aria-haspopup="true"
                        onClick={handleToggle}
                        disableRipple
                        disableFocusRipple
                      >
                        {countryInfo.name}
                      </Button>
                        <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal style = {{zIndex:100000}}>
                          {({ TransitionProps, placement }) => (
                            <Grow
                              {...TransitionProps}
                              style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom',zIndex:10000}}
                            >
                             <ClickAwayListener onClickAway={handleClickAway}>

                                <Paper elevation = {7} >
                                    <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                                      {countries.map((country,index)=><MenuItem onClick={()=>handleClose(index)}>{country}</MenuItem>)}
                                    </MenuList>
                                </Paper>
                                </ClickAwayListener>

                            </Grow>
                          )}
                        </Popper>
                    </Grid>
                    <Grid item xs={5}>
                    </Grid>
                      <Grid item xs={4}>
                        <Typography variant = "h6" className={classes.rightMenu} align = "right">
                            <Link className = {classes.menulink} to="/admin/retailers">
                                Retailers  | 
                            </Link >
                            <Link className = {classes.menulink} to="/admin/feeds"  >
                                Feeds  | 
                            </Link >
                            <Link className = {classes.menulink} to="/admin/products?q=">
                                Products |
                            </Link >
                            
                            <IconButton
                                ref = { anchorRefLog }
                                aria-controls = { openLog ? 'menu-list-grow' : undefined }
                                aria-haspopup = "true"
                                onClick = { handleToggleLog }
                            >
                                <AccountCircleIcon style={{ fontSize: 40 }}/>
                            </IconButton>
                            
                            <Popper open = { openLog } anchorEl = { anchorRefLog.current } role = { undefined } transition  disablePortal style = {{zIndex:100000}}>
                              {({ TransitionProps, placement }) => (
                                <Grow
                                  {...TransitionProps}
                                  style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                                >
                                  <Paper>
                                    <ClickAwayListener onClickAway = { handleCloseLog }>
                                      <MenuList autoFocusItem = { openLog } id="menu-list-grow" onKeyDown = { handleListKeyDownLog }>
                                        <Link className = {classes.menulink} to = '/admin/settings'>
                                            <MenuItem onClick={handleCloseLog} >
                                                <ListItemIcon>
                                                    <SettingsIcon/>
                                                </ListItemIcon>
                                                <ListItemText primary="Settings"/>
                                            </MenuItem>
                                        </Link>
                                        <Link className = {classes.menulink} to="/admin">
                                            <MenuItem onClick={handleCloseLog} >
                                                <ListItemIcon>
                                                    <ExitToAppIcon/>
                                                </ListItemIcon>
                                                <ListItemText primary="Logout"/>
                                            </MenuItem>
                                        </Link >
                                      </MenuList>
                                    </ClickAwayListener>
                                  </Paper>
                                </Grow>
                              )}
                            </Popper>
                        </Typography>
                      </Grid>
                  </Grid>
              </Hidden >
              <Hidden mdUp>
                <Grid className = {classes.topSpace}></Grid>  
              </Hidden>  
              

            <Grid container direction = "row" justify = "center" alignItems = "flex-end" spacing = {0}>
              <Grid item xs={1}>

                <Hidden mdUp>
                    <Paper className={classes.paper}>
                        <Drawer />
                    </Paper>
                </Hidden>
              </Grid>
              <Grid item xs={10}>

                {!(window.location.pathname==="/") &&<Logo/>}
                
              </Grid>
              
              <Grid item xs={1} >

                <Paper className={classes.paper}>

                  { (window.location.pathname==="/admin/products") && <ExpandSearch/>}
                  
                </Paper>
                  
              </Grid>

            </Grid>

          </Container>
          </div>
      </div>
  );
}

