import React, { useState, useContext} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { useHistory } from "react-router-dom";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://modacompris.com/">
                modacompris.com
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        '& > * + *': {
          marginTop: theme.spacing(2),
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

function isPassValid(str){
    let formIsValid = true;

    //Name
    if(str == null){
       formIsValid = false;
    }

    if(typeof str !== "undefined"){
       if(str != null && !str.match(/^[a-zA-Z]+$/)){
          formIsValid = false;
       }        
    }

    return formIsValid;
}

export default function SignIn({Close}) {
    const classes = useStyles();
    const [searchTerm, setSearchTerm] = useState("");
    const [isFocussed, setFocussed] = useState(false);
    const [name, setName] = useState("");
    const [errorName, setErrorName] = useState('#FFFFFF');
    const [pass, setPass] = useState("");
    const [errorPass, setErrorPass] = useState('#FFFFFF');
    const [open, setOpen] = useState(false);
    // const [errorLogin, setErrorLogin] = useState(false);
    const history = useHistory()

    const handleChange = (e) => {
        switch(e.target.name){
            case "name":
                setName(e.target.value);
                setErrorName('#FFFFFF');
                return;
            case "pass":
                setPass(e.target.value);
                setErrorPass('#FFFFFF');
                return;
        }
    }

    const handleEnterDown = (e) => {     
        if (e.key === "Enter") {
            e.preventDefault();
            switch(e.target.name){
                case "name":
                    setName(e.target.value);
                    setErrorName('#FFFFFF');
                    return;
                case "pass":
                    setPass(e.target.value);
                    setErrorPass('#FFFFFF');
                    return;
            }
        }
    }

    const handleButton = (e)=>{

        // if ((window.location.pathname==="/search")) {
        //   Close();
        // } 
        e.preventDefault();
        const newurl = `https://modacompris.com/api/login/`;
        fetch(newurl, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: name,
                pass: pass,
            })
        }).then(res => res.json()).then((response) => {
            if(response.results == 'ok'){
                localStorage.setItem("token", response.token);
                localStorage.setItem("refreshToken", response.refreshToken);
                let url = new URL(window.location.href);
                let oldSearchQuery = new URLSearchParams(url.search.slice(1));
                let country = oldSearchQuery.get('country');
                let newSearchQuery = new URLSearchParams();
                if(country != null){
                newSearchQuery.set('country',country)
                }
                // newSearchQuery.set('search_q',searchTerm);
                newSearchQuery.set('q',searchTerm);
                newSearchQuery.delete('category');
                newSearchQuery.delete('size');
                newSearchQuery.delete('brand');
            
                history.push({
                    pathname: '/admin/products',
                    search:newSearchQuery.toString().toLocaleLowerCase()
                });
            } else if(response.results == 'name error'){
                // alert("Name is wrong. Please try again.");
                setErrorName('#FF1744');
                setPass('');
            } else if(response.results == 'password error'){
                // alert("Password is wrong. Please try again.")
                setErrorPass('#FF1744');
                setPass('');
            }
        }).catch(error => {
            // alert(error.message);
            setOpen(true);
        }); 
        
    }

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.root}>
                <Snackbar open={open} autoHideDuration={1500} onClose={handleClose}>
                    {
                        <Alert onClose={handleClose} severity="error">
                            Login was failed. Please try again.
                        </Alert>
                    }
                </Snackbar>
            </div>

            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Admin
                </Typography>
                <form className={classes.form} noValidate>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id = "name"
                        label = "Name"
                        name = "name"
                        autoComplete = 'off'
                        value = { name }
                        onChange={handleChange}
                        onKeyDown={ (e) => handleEnterDown(e) }
                        autoFocus
                    />
                    <FormHelperText>
                        <Typography variant = "h5" style = {{color: errorName}} >
                            Name is wrong. Please try again.
                        </Typography>
                    </FormHelperText>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="pass"
                        label="Password"
                        type="password"
                        id="password"
                        // autoComplete="current-password"
                        autoComplete = "off"
                        value = { pass }
                        onChange={handleChange}
                        onKeyDown={ (e) => handleEnterDown(e) }
                    />
                    <FormHelperText>
                        <Typography variant = "h5" style = {{color: errorPass}} >
                            Password is wrong. Please try again.
                        </Typography>
                    </FormHelperText>
                    {/* <FormControlLabel
                        control={<Checkbox value="remember" color="primary" />}
                        label="Remember me"
                    /> */}
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={ handleButton }
                    >
                        Log In
                    </Button>
                </form>
            </div>
            <Box mt={8}>
                <Copyright />
            </Box>
        </Container>
    );
}