import React,{useState,useContext} from 'react';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Button from '@material-ui/core/Button';
import Popper from '@material-ui/core/Popper';
import Paper from '@material-ui/core/Paper';
import MenuList from '@material-ui/core/MenuList';
import Grow from '@material-ui/core/Grow';
import MenuItem from '@material-ui/core/MenuItem';
import { useHistory } from "react-router-dom";
import {CountryContext} from './CountryContext';


const countries = [
    //'All countries',
    'United Kingdom',
    'Austria',
    'Belgium',
    'Czech Republic',
    'France',
    'Germany',
    'Greece',
    'Hungary',
    'Ireland',
    'Italy',
    'Netherlands',
    'Poland',
    'Portugal',
    'Romania',
    'Spain',
];

function CountrySelector() {
    const history = useHistory()
    const [open, setOpen] = useState(false);
    const anchorRef = React.useRef(null);
    const [countryInfo, setCountryInfo,selected, setSelected,page,setPage] = useContext(CountryContext);


    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
      };
      const handleClickAway = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
          return;
        }
        setOpen(false);
      };
      const handleClose = (index) =>{
        setOpen(false);
        setCountryInfo({name:countries[index]});
        setSelected([]);
        setPage(1);
        
        let url = new URL(window.location.href);
        let searchQuery = new URLSearchParams();
        let newSearchQuery = new URLSearchParams(url.search.slice(1));
        var searchTerm = newSearchQuery.get('q');
        searchQuery.set('country',countries[index]);
        searchQuery.set('q',searchTerm);
        newSearchQuery.set('country',countryInfo.name)
        
        if (window.location.pathname==="/search") {
          history.push({
            pathname: '/search',
            search: searchQuery.toString().toLowerCase(),
            state: true
          })
        }else if(window.location.pathname.slice(0,10)==="/retailers"){
          history.push(`/retailers/${countries[index].toLowerCase()}`);
        }
      }
     const handleListKeyDown = (event)=> {
        if (event.key === 'Tab') {
          event.preventDefault();
          setOpen(false);
        }
      }
      
    return (
        <div>
            <Button
            ref={anchorRef}
            aria-controls={open ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={handleToggle}
            disableRipple
            disableFocusRipple
            >
            {countryInfo.name}
            </Button>
            <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition style = {{zIndex:100000}}>
            {({ TransitionProps, placement }) => (
                <Grow
                {...TransitionProps }
                // timeout={700}
                style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom',zIndex:10000}}
                >
                <ClickAwayListener onClickAway={handleClickAway}>

                    <Paper elevation = {7} >
                        <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown} >
                            {countries.map((country,index)=><MenuItem onClick={()=>handleClose(index)} dense = {true}>{country}</MenuItem>)}
                        </MenuList>
                    </Paper>
                </ClickAwayListener>

                </Grow>
            )}
            </Popper>
        </div>
    )
}

export default CountrySelector;

