import React,{useState,useEffect,useContext,useRef} from "react";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import IconButton from '@material-ui/core/IconButton';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Checkbox from '../CustomCheckbox/CustomCheckbox'
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography'
import { Container, Collapse, Box, Fade} from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import PropTypes from 'prop-types';
import { useHistory } from "react-router-dom";
import {BackendQuery} from '../../Pages/Search/BackendQuery'
import { FixedSizeList } from "react-window";
import {CountryContext} from '../../Components/Header/CountryContext'

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
    padding: theme.spacing(0.5),
  },
  chip: {
    margin: theme.spacing(0.5),
    transitionDuration:'1s',
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  //IconButton Style
  IBtnRoot:{
    padding:0,
  },
  menuContainer:{
    boxShadow:'none',
    borderRadius:0,
    backgroundColor:'#EAEAEA'
  },
}));

export default function Menu(props) {

  const [sort, setsort] = useState(false);
  const [sizeCallaps, setsizeCallaps] = useState(false);
  const [brandCallaps, setbrandCallaps] = useState(false);
  const [sortCallaps, setsortCallaps] = useState(false);
  const [categoryCallaps, setcategoryCallaps] = useState(false);
  const history = useHistory();
  const [selectedSort, setSelectedSort] = useState(true);
  const [menuData] = props.menu;
  const [,,selected,setSelected,,setPage] = useContext(CountryContext);
  

  console.log(useContext(CountryContext));

  const DeleteQueryItem =(menuKind,value) =>{

    let url = new URL(window.location.href);
    let searchParams = new URLSearchParams(url.search.slice(1));
    let newSearchParams = new URLSearchParams();
   
    for(var pair of searchParams.entries()) {
      if ((pair[1].toLowerCase() !== value.toLowerCase()) || 
      (pair[0].toLowerCase() !== menuKind.toLowerCase())) {
        newSearchParams.append(pair[0],pair[1]);
      }
    }
    history.push({
      search: newSearchParams.toString().toLowerCase(),
    })
  }
  const AddQueryItem = (menuKind,value) => {
      let url = new URL(window.location.href);
      let newSearchQuery = new URLSearchParams(url.search.slice(1));
      newSearchQuery.append(menuKind,value.toLowerCase());
      history.push({
        search: newSearchQuery.toString().toLowerCase(),
      })
  }

  const handleChange =(menuKind,key)=>{
    const selectedIndex = selected.map(item=>item.key).indexOf(key);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, {key:key,menuKind:menuKind});
      AddQueryItem(menuKind,key);
     
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
      DeleteQueryItem(menuKind,key);
      
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
      DeleteQueryItem(menuKind,key);
     
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
      DeleteQueryItem(menuKind,key);
    }
    setSelected(newSelected);
     setPage(1);
  }
  const closeCollapse = ()=>{
    setcategoryCallaps(false);
    setsizeCallaps(false);
    setbrandCallaps(false);
    setsortCallaps(false);
  }

  const handleSortChange = event => {
    let url = new URL(window.location.href);
    let newSearchQuery = new URLSearchParams(url.search.slice(1));

    if(event.toString() === "asc"){
      setSelectedSort(true);
      newSearchQuery.set('sort','asc')
       history.push({
        search: newSearchQuery.toString().toLowerCase(),
      })
    } else {
      setSelectedSort(false);
      newSearchQuery.set('sort','desc')
       history.push({
        search: newSearchQuery.toString().toLowerCase(),
      })
    }
  }
  const handleTopCheck = (name)=>{
    setSelected(selected=>selected.filter(item=>item.menuKind!==name));
    let url = new URL(window.location.href);
    let newSearchQuery = new URLSearchParams(url.search.slice(1));

    newSearchQuery.delete(name);
    history.push({
      search: newSearchQuery.toString().toLowerCase(),
    })
  }

  const matches = useMediaQuery('(min-width:1000px)');
  const toggleCollapse = (menuName)=>e=>{
    if (!matches) { 
      if (menuName === "category") {
        setcategoryCallaps(!categoryCallaps);
        setsizeCallaps(false);
        setbrandCallaps(false);
        setsortCallaps(false);
      }
      if (menuName === "size") {
        setcategoryCallaps(false);
        setsizeCallaps(!sizeCallaps);
        setbrandCallaps(false);
        setsortCallaps(false);
      }
      if (menuName === "brand") {
        setcategoryCallaps(false);
        setsizeCallaps(false);
        setbrandCallaps(!brandCallaps);
        setsortCallaps(false);
      }
      if (menuName === "sort") {
        setcategoryCallaps(false);
        setsizeCallaps(false);
        setbrandCallaps(false);
        setsortCallaps(!sortCallaps);
      }
    }else{
      setcategoryCallaps(!categoryCallaps);
      setsizeCallaps(!categoryCallaps);
      setbrandCallaps(!categoryCallaps);
      setsortCallaps(!categoryCallaps);
    }  
  }
  
  const classes = useStyles();
  const isSelected = (name) => selected.map(item=>item.key).indexOf(name) !== -1;

    const CategoryRow = ({ index, style }) => (
      <div  style={{...style,paddingTop:"-10px"}}>
        <Checkbox
          key = {menuData.category[index]}
          checked={isSelected(menuData.category[index])}
          onChange={(e)=>handleChange(e.target.name,menuData.category[index])}
          name="category"
          color="primary"
        />
        {menuData.category[index].slice(0,10)}
      </div>
    );
  
    const SizeRow = ({ index, style }) => (
      <div  style={{...style,paddingTop:"-10px"}}>
        <Checkbox
          key = {menuData.size[index]}
          checked={isSelected(menuData.size[index])}
          onChange={(e)=>handleChange(e.target.name,menuData.size[index])}
          name="size"
          color="primary"
        />
        {menuData.size[index].slice(0,10)}
      </div>
    );
  
    const BrandRow = ({ index, style }) => (
      <div  style={{...style,paddingTop:"-10px"}}>
        <Checkbox
          key = {menuData.brand[index]}
          checked={isSelected(menuData.brand[index])}
          onChange={(e)=>handleChange(e.target.name,menuData.brand[index])}
          name="brand"
          color="primary"
        />
        {menuData.brand[index].slice(0,10)}
      </div>
    );
  
  return (
    <div>
      {console.log("This si selected")}
      {console.log(useContext(CountryContext))}
      <ClickAwayListener onClickAway = { closeCollapse }>
        <Container maxWidth = "lg" style = {{marginTop:"50px"}}>
          <Paper  className = {classes.menuContainer}>
            <Grid container xs = {12} >
              <Grid item xs = {12} md = {2}>
                <Box ml = {2}style = {{marginTop:'12px'}}>
                  <Typography variant = "h6"  component = "h2">
                    Refine your search  
                  </Typography>
                </Box>
              </Grid>
              <Grid container xs = {12} md = {2}>
                <Grid item >
                   <Checkbox
                    checked ={selected.filter(item=>item.menuKind==='category').length>0}
                    onChange = {(e)=>handleTopCheck(e.target.name)}
                    name = "category" 
                    disableRipple
                    disableFocusRipple
                  />
                  <Button onClick = { toggleCollapse('category') }> category </Button>
                  <IconButton
                    className={clsx(classes.expand, {
                      [classes.expandOpen]: categoryCallaps,
                    })}
                    onClick={ toggleCollapse('category') }
                    aria-categoryCallaps={ categoryCallaps }
                    aria-label="show more"
                    classes={{
                      root: classes.IBtnRoot, 
                    }}
                  >
                    <ExpandMoreIcon />
                  </IconButton>
                  <Collapse in = {categoryCallaps} timeout = {1000}>     
                    <Grid style = {{paddingLeft:'10px',paddingTop:'20px'}}>
                      {
                        menuData&&
                        <div>
                          <FixedSizeList
                          height={300}
                          width={180}
                          itemSize={30}
                          itemCount={menuData.category.length}
                          
                          >
                          {CategoryRow}
                          </FixedSizeList>
                        </div>
                      }
                    </Grid>
                  </Collapse>
                </Grid>
              </Grid> 
              <Grid container xs = {12} md = {2}>
                <Grid item >
                  <Checkbox
                      checked ={selected.filter(item=>item.menuKind==='size').length>0}
                      name = "size" 
                      disableRipple
                      disableFocusRipple
                      onChange = {(e)=>handleTopCheck(e.target.name)}

                    />
                    <Button onClick = {toggleCollapse('size')}>size</Button>
                    <IconButton
                      className = {clsx(classes.expand, {
                        [classes.expandOpen]: sizeCallaps,
                      })}
                      onClick={toggleCollapse('size')}
                      aria-sizeCallaps={sizeCallaps}
                      classes={{
                        root: classes.IBtnRoot, 
                      }}
                      >
                      <ExpandMoreIcon />
                    </IconButton>
                  <Collapse in = {sizeCallaps} timeout = {1000}>
                    <Grid style = {{paddingLeft:'10px',paddingTop:'20px'}}>
                    {
                     menuData&&
                      <div>
                        <FixedSizeList
                        height={300}
                        width={180}
                        itemSize={30}
                        itemCount={menuData.size.length}
                        >
                          {SizeRow}
                        </FixedSizeList>
                      </div>
                      }
                    </Grid>
                  </Collapse>
                  </Grid>
              </Grid>              
              <Grid container xs = {12} md = {2}>
                <Grid item >
                  <Checkbox
                      checked ={selected.filter(item=>item.menuKind==='brand').length>0}
                      name = "brand" 
                      disableRipple
                      disableFocusRipple
                      onChange = {(e)=>handleTopCheck(e.target.name)}

                      />
                  <Button onClick = {toggleCollapse('brand')}>brand</Button>
                  <IconButton
                    className = { clsx( classes.expand, {
                      [classes.expandOpen]: brandCallaps,
                    })}
                    onClick={toggleCollapse('brand')}
                    aria-brandCallaps={brandCallaps}
                    classes={{
                      root: classes.IBtnRoot, 
                    }}
                    >
                    <ExpandMoreIcon />
                  </IconButton>
                  <Collapse in = {brandCallaps} timeout = {1000}>
                    <Grid style = {{paddingLeft:'10px',paddingTop:'20px'}}>
                        {
                          menuData&&
                          <div>
                            <FixedSizeList
                            height={300}
                            width={180}
                            itemSize={30}
                            itemCount={menuData.brand.length}
                            >
                              {BrandRow}
                            </FixedSizeList>
                          </div>
                        }
                    </Grid>
                  </Collapse>
                </Grid>
              </Grid> 
              <Grid container xs = {12} md = {2}>
                <Grid item >
                  <Checkbox
                    checked ={sort}
                    name = "sort" 
                    disableRipple
                    disableFocusRipple
                    />
                  <Button onClick = {toggleCollapse('sort')}>sort By</Button>
                  <IconButton
                    className={clsx(classes.expand, {
                      [classes.expandOpen]: sortCallaps,
                    })}
                    onClick={toggleCollapse('sort')}
                    aria-sortCallaps={sortCallaps}
                    aria-label="show more"
                    classes={{root: classes.IBtnRoot}}
                    >
                    <ExpandMoreIcon />
                  </IconButton>
                  <Collapse in = {sortCallaps} timeout = {1000}>
                    <Grid style = {{paddingLeft:'20px',paddingTop:'20px'}}>
                      <Grid xs = {12}>
                        <FormControlLabel
                          control={
                            <Checkbox
                              key = 'sort'
                              checked = { selectedSort }
                              onChange = { (sortEvent) => handleSortChange(sortEvent.target.name)}
                              name = 'asc'
                              color="primary"
                            />
                          }
                          label = 'Price - Low to High'
                        />
                      </Grid>
                      <Grid xs = {12}>
                          <FormControlLabel
                            control={
                              <Checkbox
                                key = 'sort'
                                checked = { !selectedSort }
                                onChange = { (event)=>handleSortChange(event.target.name)}
                                name = 'desc'
                                color="primary"
                              />
                            }
                            label = 'Price - High to Low'
                          />
                        </Grid>
                    </Grid>
                  </Collapse>
                </Grid>
              </Grid> 
            </Grid>
          </Paper>
        </Container>
      </ClickAwayListener>         
    </div>
  )
}
Menu.prototype = {
  menuData: PropTypes.array,
}