export const onHandleOneProduct = (id,active) => {
    
    (active==="true")?active = "false":active = "true"
    const newurl = `https://modacompris.com/api/products/${id}/${active}`;
    return fetch(newurl, {
      method: 'Put',
      headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
      },
    }).then(res => res.json()).then((response) => {
        if(response.results === 'ok'){
            return true;
        } else if(response.results === 'Error'){
            return false;
        }
    }).catch(error =>{
        return false;
    }
    );
  }
  